@charset "UTF-8";
article {
  margin: 20px 0;
}

.single-meta {
  font-size: 0.875rem;
  margin-bottom: 20px;
}

.single-breadcrumbs {
  margin: 0;
  font-weight: 700;
  font-size: 0.875rem;
  padding: 0;
  display: flex;
  gap: 8px;
  align-items: center;
}
.single-breadcrumbs li {
  list-style: none;
  line-height: 24px;
  height: 24px;
}
.single-breadcrumbs li:not(:first-child) {
  list-style-type: "â€º";
  margin: 0 4px;
}
.single-breadcrumbs li:not(:first-child)::marker {
  font-weight: 700;
}
.single-breadcrumbs .single-home-icon {
  height: 24px;
}

.single-header {
  display: flex;
  flex-direction: column;
  gap: 4px;
  align-items: center;
}
.single-header .category-badge--with-background {
  align-self: auto;
  color: #fff;
}
.single-header .single-title {
  font-size: 3rem;
  line-height: 115%;
  text-align: center;
}
.single-header .single-excerpt {
  width: clamp(200px, 80%, 900px);
}
.single-header .single-feature-image {
  margin: 20px 0;
  width: clamp(200px, 80%, 900px);
  height: auto;
  aspect-ratio: 16/9;
  object-fit: cover;
}

.single-body-container {
  display: flex;
}
.single-body-container .content-container {
  margin: 0 auto;
  width: 670px;
}
.single-body-container .content-container p {
  line-height: 1.5rem;
  word-wrap: break-word;
  overflow-wrap: break-word;
  hyphens: auto;
  margin: 1rem 0;
}
.single-body-container .content-container p:first-child {
  margin-top: 0;
}
.single-body-container .content-container p:last-child {
  margin-bottom: 0;
}
.single-body-container .socials-container .socials-header {
  font-size: 1.25rem;
  font-weight: 700;
}
.single-body-container .socials-container .socials-icons {
  margin: 16px 0;
  display: flex;
  gap: 8px;
}

.ad-example-space .example-ad {
  width: 240px;
  height: 400px;
  background-color: black;
}